<template>
    <div>
        <!-- 头部 -->
       <div class="header">
            <van-nav-bar
                title="购物车"
                right-text="..."
                left-arrow
                @click-left="onClickLeft"
                @click-right="onClickRight"
            />
       </div>
        <!-- 头部 结束-->
      
      <!-- 标题 -->
        <div class="choose" v-show="($store.state.cart.lists.length)">
            <van-sticky >
            <div class="title" >
                    <van-col span="3">
                        <van-checkbox @click="checkedAll" :value="checked" ></van-checkbox>
                    </van-col> 
                    <van-col span="3">
                        <img src="../assets/images/nav4.png" alt="">
                    </van-col> 
                    <van-col span="18">
                        <span>欢迎选购</span>
                    </van-col> 
            </div>
            </van-sticky>
        </div>
        <!-- 标题 结束-->
      

       <!-- main 商品列表 -->
       <div class="main" >

             <!-- 购物车没有商品时显示-->
            <div v-show="!($store.state.cart.lists.length)" class="shoplist" >
                <div class="goodsinfo">
                    <p><img src="../assets/images/toshop.png" alt=""></p>
                    <p>空空如也</p>
                </div>
                <van-divider>猜你喜欢</van-divider>

                <!-- 推荐 -->
                <foryou></foryou>

            </div>


        
           <div class="shoplist" v-for = "(item,index) in lists" :key="index">
                <van-row>
                    <van-col span="2">
                         <van-checkbox  :value="item.isSelect" @click="inputselect(index)"></van-checkbox>
                          <!-- <input type="checkbox" :checked="item.isSelect" @click="inputselect(index)" > -->
                    </van-col>
                    <van-col span="8">
                        <van-image
                            height="100px"
                            fit="contain"
                            :src="item.imgarr"
                        />
                    </van-col>
                    <van-col span="14">
                        <div class="content">
                            <h1>{{item.name}}</h1>
                            <h2>{{item.des}}</h2>
                            <span class="dec">价格实惠</span>
                            <div class="price">
                                <span class="money">￥{{item.price}}.00</span>
                                <div class="num">
                                   <button @click="item.num <= 1 ? 1:item.num--">-</button>
                                    <span>{{item.num}}</span>
                                    <button @click="item.num++">+</button>
                                </div>
                            </div>
                            <p>
                                <button @click="del(index)">删除</button>
                            </p>
                        </div>
                    </van-col>
                </van-row>
            </div>
       </div>
       <!-- main 商品列表 end -->

       <!-- 提交订单 -->
     <van-submit-bar  v-show="$store.state.cart.lists.length" :price="total * 100" button-text="提交订单" @submit="onsubmit">
            <van-checkbox @click="checkedAll" :value="checked">全选</van-checkbox>
        </van-submit-bar>
       <!-- 提交订单结束 -->

        <!-- 底部导航 -->
        <botnav></botnav>


    </div>
</template>

<script>
// 引入为你推荐
import foryou from "../components/public/foryou"
// 引入导航
import botnav from "../components/public/botnav"


// 引入顶部样式
import { Toast } from 'vant';

    export default {
        data() {
            return {
                checked:true,
                total:0,
            //    lists :[
                    // {
                    //     isSelect:true,
                    //     name:"华为(HUAWEI)p30",
                    //     des:"超大屏幕 128G内存 新品上线",
                    //     price:3000,
                    //     num:1,
                    //     imgarr:require('../assets/images/huawei.jpg')
                    // },
                    //  {
                    //     isSelect:true,
                    //     name:"爱马仕背包",
                    //     des:"轻小,容量大，颜色黑",
                    //     price:300,
                    //     num:1,
                    //     imgarr:require('../assets/images/seckill_1.jpg'),
                    // },
                    //  {
                    //     isSelect:true,
                    //     name:"华为(HUAWEI)p30",
                    //     des:"超大屏幕 128G内存 新品上线",
                    //     price:10,
                    //     num:1,
                    //     imgarr:require('../assets/images/huawei.jpg'),
                    // },
                    // {
                    //     isSelect:true,
                    //     name:"华为(HUAWEI)p30",
                    //     des:"超大屏幕 128G内存 新品上线",
                    //     price:10,
                    //     num:1,
                    //     imgarr:require('../assets/images/huawei.jpg'),
                    // },


            //    ]
            lists:[],
           
            };
        },
         methods: {
          
            onClickLeft() {
                this.$router.go(-1)
            },
            onClickRight() {
            Toast('按钮');
            },
            // 删除功能
            del(index){
                this.lists.splice(index,1)
            },
            sum(){
                // 总价格计算
                // total 累加值
                this.total = this.lists.reduce((total,item)=>{
                    // 判断选项选中计算价格
                    if(!item.isSelect) return total; //返回默认价格
                    return total + parseInt(item.price) * parseInt(item.num); //总价格
                },0) //总价格默认值0
            },
            checkedAll(){
                this.lists.forEach((item)=>{
                    item.isSelect = !this.checked;
                })
                this.checked = !this.checked;
            },
            checkboxclick(){
                // 第一次加载页面过程中，判断复选框状态，全部true，返回true，有一个false,放回false
                this.checked = this.lists.every((item)=>{
                    return item.isSelect;
                })
            },
            inputselect(index){
                // 修改商品列表
                this.lists[index].isSelect = !this.lists[index].isSelect;
                this.checkboxclick();
                this.sum();
            },
            onsubmit(){
                if(this.$store.state.username){
                    this.$router.push('/order')
                }else{
                    this.$router.push({
                        path:"/login",
                        query:{
                            path:"/order"
                        }
                    })
                }
            }


        },
        components:{
            foryou,
            botnav
        },
        created(){
            this.lists = this.$store.state.cart.lists;
            this.checkboxclick();
            this.sum();
        },
        beforeUpdate(){
            this.sum();
        },
        beforeDestroy(){
            this.$store.commit("cart/edit",this.lists)
        }
       
    }
</script>

<style lang="scss" scoped>

.choose{
     margin-top:12px ;
    .title{
        padding: 0 12px;
        height: 40px;
        line-height: 40px;
        // box-sizing: border-box;
        padding-top: 10px;
        background-color: white;
        .van-checkbox{
             margin-top:7px ;
        }
        img{
            width: 24px;
            margin-top:7px ;
        }
        span{
            font-size: 18px;
            color: #000;
        }
        
    }
}

// main 商品列表开始
.main{
    // margin-top:12px ;
    .shoplist{
        margin-bottom: 12px;
        background-color: white;
        padding: 0 10px;
        padding-top: 8px;
        position: relative;
        .van-checkbox{
            position: relative;
            left:4px;
            top:34px;
        }
        .content{
            padding-left: 8px;
            h1{
                font-size: 16px;
            }
            h2{
                font-size: 14px;
                font-weight: normal;
                margin-bottom: 3px;
            }
            .dec{
                font-size: 12px;
                font-weight: normal;
                padding: 3px;
                color: red;
                background-color: #fcedeb;
               
            }
            
            .price{
                 margin-top: 3px;
                height: 30px;
                .money{
                    float: left;
                    font-size: 16px;
                    color: red;
                }
                .num{
                    float: right;
                    font-weight: bold;
                    span{
                        display: inline-block;
                        width: 20px;
                        height: 20px;
                        text-align: center;
                        line-height: 20px;
                        
                    }
                    button{
                        width: 20px;
                        height: 20px;
                        border: none;
                    }
                }
            }
            p{
                text-align: right;
                button{
                    width: 30px;
                    height: 20px;
                    line-height: 20px;
                    text-align: center;
                    color: #9d9fa1;
                    border: none;
                    background-color: #f7f7f7;
                }
            }
        }
    }
}
// main 商品列表结束


// 提交
.van-submit-bar{
    bottom:50px
}

// 购物车没有时显示
.shoplist{
    .goodsinfo{
        p{
            padding: 10px 0;
            text-align: center;
            font-size: 15px;
            img{
                width: 100px;
                margin: 0 auto;
            }
        }
        
    }
    
    
}




</style>